.home-page {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-flow: column;
        flex-flow: column;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: white;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1); /* you need a scale here to allow it to transition in both directions */
    -webkit-transition: 0s.15s all ease;
    -o-transition: 0s.15s all ease;
    transition: 0s.15s all ease;
}


/* Input styles */

.link-input {
    font-size: 45px;
    text-align: center;
    width: 50vw;
    min-width: 380px;
    margin-left: 20px;
    margin-right: 20px;
    border: none;
    background: black;
    color: white;
    border-bottom: 3px solid white;
}

.link-input:focus {
    outline: none;
    color: white;
}

/* button styles */

.action {
    font-size: 1.2rem;
    line-height: 1;
    margin: 20px;
    padding: 1.2em 2.8em;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
    font-weight: 800;
    color: white !important;
    -webkit-transition: color 300ms ease-in-out, -webkit-box-shadow 300ms ease-in-out;
    transition: color 300ms ease-in-out, -webkit-box-shadow 300ms ease-in-out;
    -o-transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
    transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
    transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out, -webkit-box-shadow 300ms ease-in-out;
}

.action:hover {
    color: black !important;
    -webkit-box-shadow: 0 0 40px 40px white inset;
            box-shadow: 0 0 40px 40px white inset;
}

.incorrect {
    font-size: 1.2rem;
    line-height: 1;
    margin: 20px;
    padding: 1.2em 2.8em;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
    font-weight: 800;
    color: white !important;
    -webkit-transition: color 300ms ease-in-out, -webkit-box-shadow 300ms ease-in-out;
    transition: color 300ms ease-in-out, -webkit-box-shadow 300ms ease-in-out;
    -o-transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
    transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
    transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out, -webkit-box-shadow 300ms ease-in-out;
}

.incorrect:hover {
    -webkit-box-shadow: 0 0 40px 40px red inset !important;
            box-shadow: 0 0 40px 40px red inset !important;
    color: white !important;
}

.not-visible {
    visibility: hidden;
}

/* top result bar */

#top {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: 25px;
    color: black;
    background: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

#top > h4 {
    margin: 0px;
    overflow: hidden;
}

#bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: 25px;
    color: black;
    background: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

#bottom:hover {
    cursor: pointer;
}


